:root{

  /* ==== colors is hsl ==== */

  --clr-black: #0f0f0f;
  --clr-white: #ffffff;

  --clr-gray-800: #1f2937;
  --clr-gray-700: #374151;
  --clr-gray-600: #4b5563;

  --clr-gray-50: #f9fafb;
  --clr-gray-100: #f3f4f6;

  // --clr-primary-one: ;
  // --clr-primary-two: hsl(,,);

  --clr-red-500: #FF5454;
  // --clr-secondary-one: hsl(,,);
  // --clr-secondary-two: hsl(,,);

  /* font */
  font-size: 62.5%;

  --fs-900: clamp(3rem, 10vw + 1rem, 12rem);
  --fs-700: clamp(2.5rem, 10vw + 1rem, 5rem);
  --fs-500: clamp(1.5rem, 2vw + 1rem, 2rem);
  --fs-400: clamp(1.25rem, 2vw + 1rem, 1.75rem);

  // --ff-primary: 'Poppins', sans-serif; // 100 400 600 700
  --ff-primary: sans-serif; 

  /* scroll-behavior: smooth; */
}

body {
  font-family: var(--ff-primary);
  font-weight: 400;
  color: var(--clr-gray-100);
  background: var(--clr-gray-800);
}
